.form-progress-container {
  display: none;
}
.form {
  padding: 0 2.4rem;
}
.questions-container {
  display: flex;
  flex-direction: column;
  row-gap: 9.6rem;
  margin-bottom: 12.1rem;
}
.question-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 7.5rem;
  margin-bottom: 1.8rem;
}
.question-header:hover {
  cursor: pointer;
}
.question-disabled {
  opacity: 0.4;
}
.question-disabled:hover {
  cursor: not-allowed;
}
.question-header h2 {
  font-size: 2.4rem;
  line-height: 2.8rem;
  color: var(--grey);
}
.question-header .arrow {
  width: 3rem;
}
.answers-container {
  display: flex;
  flex-direction: column;
  row-gap: 1.6rem;
}
.answer {
  background-color: var(--big-selection-option-default);
  border-radius: 0.8rem;
  padding: 2.4rem 2.5rem;
}
.answer:hover {
  cursor: pointer;
}
.answer h3 {
  font-size: 2.4rem;
  line-height: 3.2rem;
  margin-bottom: 0.8rem;
}
.answer p {
  font-size: 1.6rem;
  line-height: 2.6rem;
}
.order-summary {
  background-color: #2c343e;
  border-radius: 1rem;
  padding: 3.2rem 2.4rem;
  margin-bottom: 5.6rem;
}
.order-summary .title {
  color: #ffffff;
  opacity: 0.5;
  text-transform: uppercase;
  margin-bottom: 0.8rem;
}
.order-summary .description {
  font-size: 2.4rem;
  line-height: 4rem;
  color: #ffffff;
  font-weight: 900;
  font-family: "Fraunces", serif;
}
.order-summary span {
  color: var(--dark-cyan);
}
.button-container {
  text-align: center;
}
/* Question open */
.question-open .arrow {
  transform: rotate(180deg);
  margin-right: 1rem;
}
.selected-answer {
  background-color: var(--big-selection-option-active);
}
.selected-answer h3,
.selected-answer p {
  color: #ffffff;
}

/* Tablet */
@media (min-width: 768px) {
  .form {
    padding: 0 4rem;
  }
  .question-header h2 {
    font-size: 3.2rem;
    line-height: 4.8rem;
  }
  .answers-container {
    flex-direction: row;
    column-gap: 1rem;
  }
  .answer {
    padding: 3.2rem 2.5rem 8.4rem;
  }
  .order-summary {
    padding: 2.7rem 4.4rem;
    margin-bottom: 4rem;
  }
}

/* Desktop */
@media (min-width: 1024px) {
  .form {
    padding: 0 8.5rem;
  }
}
@media (min-width: 1280px) {
  .form-container {
    padding: 0 12rem;
  }
}
@media (min-width: 1440px) {
  .form-progress-container {
    display: initial;
  }
  .form-container {
    display: flex;
    column-gap: 12.5rem;
    padding: 0 16.5rem;
  }
  .form {
    padding: 0;
    max-width: 73rem;
  }
  .question-open .answer:hover {
    cursor: pointer;
    background-color: var(--big-selection-option-hover);
  }
  .button-container {
    text-align: right;
  }
}
