.title-container {
  position: relative;
  margin-bottom: 1.3rem;
}
.title-layer {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  background: linear-gradient(
    180deg,
    rgba(254, 252, 247, 0.0001) 0%,
    #fefcf7 100%
  );
}
.title-container h2 {
  color: var(--grey);
  text-align: center;
  line-height: 7.2rem;
}
.products-container {
  display: flex;
  flex-direction: column;
  row-gap: 4.8rem;
  padding: 0 2.2rem;
}
.product {
  text-align: center;
}
.product img {
  width: 20rem;
  margin-bottom: 2.4rem;
}
.product h3 {
  font-size: 2.4rem;
  line-height: 3.2rem;
  margin-bottom: 1.6rem;
}
.product p {
  font-size: 1.5rem;
  line-height: 2.5rem;
}

/* Tablet */
@media (min-width: 768px) {
  .title-container {
    margin-bottom: 0;
  }
  .title-layer {
    height: 10rem;
  }
  .title-container h2 {
    font-size: 9.6rem;
    opacity: 0.5;
  }
  .products-container {
    margin-top: -2.5rem;
    padding: 0 8rem;
    position: relative;
    z-index: 7;
  }
  .product {
    display: flex;
    text-align: left;
    column-gap: 3.6rem;
  }
  .product img {
    height: 17.5rem;
    width: 25.5rem;
    margin-bottom: 0;
  }
  .product h3 {
    margin-top: 1.5rem;
    margin-bottom: 2.4rem;
  }
}

/* Desktop */
@media (min-width: 1400px) {
  .title-container h2 {
    font-size: 15rem;
  }
  .products-container {
    padding: 0 8.5rem;
  }
  .products-container {
    flex-direction: row;
  }
  .product {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .product h3,
  .product p {
    text-align: center;
  }
}
